<template>
  <div class="showContent checkInfoContent">
    <requestAndHisSubMenu :routekey="routekey"></requestAndHisSubMenu>
    <div class="box">
      <h3>Company Information</h3>
      <a-form
        :layout="formLayout"
        @submit="submitInfor"
        :autoFormCreate="(form)=>{this.form = form}"
        :hideRequiredMark="true"
        class="checkInforForm"
      >
        <a-form-item
          label="Company name"
          fieldDecoratorId="companyName"
          :fieldDecoratorOptions="{rules: [{ required: true,whitespace:true, message: 'please enter' }]}"
          :labelCol="{span:6}"
          :wrapperCol="{span:18}"
        >
          <a-input>
            <!-- <a-icon slot="prefix" type='user' style="color:rgba(0,0,0,.25)"/> -->
          </a-input>
        </a-form-item>
        <a-form-item
          label="Company code"
          fieldDecoratorId="companyZipCode"
          :fieldDecoratorOptions="{rules: [{ required: true,whitespace:true, message: 'please enter' }]}"
          :labelCol="{span:6}"
          :wrapperCol="{span:18}"
        >
          <a-input>
            <!-- <a-icon slot="prefix" type='user' style="color:rgba(0,0,0,.25)"/> -->
          </a-input>
        </a-form-item>
        <a-form-item class="rightItem">
          <a-button>Reset</a-button>
          <a-button type="primary" htmlType="submit">Check</a-button>
        </a-form-item>
      </a-form>
    </div>
    <p class="result" v-if="checkResult">Result</p>
    <div class="box" v-if="checkResult">
      <div class="infor_form">
        <div>
          <!-- 公司名 -->
          <label>Company name</label>
          <input type="text"  readonly>
        </div>
        <div>
          <!-- 公司地址 -->
          <label>Company address</label>
          <input type="text"  readonly>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import requestAndHisSubMenu from "@/components/requestAndHisSubMenu";
export default {
  data() {
    return {
      routekey: ["companyInfo", "companyInfoHistory"],
      formLayout: "inline",
       checkResult:"",
    };
  },
  components: {
    requestAndHisSubMenu
  },
  methods: {
    submitInfor(e) {
      e.preventDefault();
      this.form.validateFields((err, values) => {
        if (!err) {
          console.log(values);
          this.$store.dispatch("companyCheck", values).then(
            res => {
              console.log(res);
              this.checkResult=res.data;
            },
            error => {
              console.log(error);
              if(error.code==400){  //  Company Zip Code is invalid
              this.$message.error('Company Zip Code is invalid');
              }
            }
          );
        }
      });
    },
  }
};
</script>

<style lang="less">
@import "../../../css/checkInfor.less";
</style>



